/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Who is Edmund Stoiber?","date":"12 Jan 2002","slug":"/2002-01-12-who-is-edmund-stoiber/","tags":"Y2002"},"html":"<p><a href=\"http://www.washingtonpost.com/wp-dyn/articles/A34754-2002Jan12.html\">Washington Post:</a> \"Stoiber is a lawyer by training. He has a clean-living image, to the point of being known for merely sipping a little beer in a state renowned for guzzling it.\"<br />\n(via <a href=\"http://www.nothingbutthetruth.de/stories.php?story=02/01/12/1668326\">Nothing But The Truth</a>)</p>"},"next":{"fields":{"title":"Leftover Lutefisk","date":"12 Jan 2002","slug":"/2002-01-12-leftover-lutefisk/"}},"previous":{"fields":{"title":"Foreigners Must Wait Here!","date":"13 Jan 2002","slug":"/2002-01-13-foreigners-must-wait-here/"}}},{"node":{"fields":{"title":"Leftover Lutefisk","date":"12 Jan 2002","slug":"/2002-01-12-leftover-lutefisk/","tags":"Y2002"},"html":"<p>I'm going to try posting items about Germany at my Movable Type site http://www.papascott.de, but I'll link to some of them here. For example:</p>\n<p><a href=\"http://www.papascott.de/\">papascott.de</a>: <a href=\"/2002/01/12/index.php#000101\">Who is Edmund Stoiber?</a><br />\n<hr />\n<p>Back in December, I mentioned that we would be <a /2001/12/22>eating lutefisk for our Hanson Christmas dinner. <a href=\"http://hal.editthispage.com/\">Hal</a> remembered a <a href=\"http://hal.editthispage.com/2001/12/22\">childhood lutefisk dinner</a> with his Swedish relatives, and I made the <a href=\"http://shanson.editthispage.com/2001/12/23\">intelligent comment</a> \"I thought Swedes had more sense than to eat lutefisk\".</p>\n<p>On Christmas Day I missed a posting in my discussion group, since I was too busy <span style=\"text-decoration: line-through;\">recovering from the lutefisk dinner</span> being sick and partaking of that American Christmas tradition of watching the John Wayne marathon on WTBS. Namely, <a href=\"http://jonasbeckman.editthispage.com\">Jonas</a> (from Stockholm) <a href=\"http://shanson.editthispage.com/discuss/msgReader$811?mode=day\">wrote</a>  \"Unfortunately, Swedes don't have more sense than that. Thanks for thinking we should, though. We call it lutfisk - and in fact I often eat it during Christmas. But I do stay clear of <a href=\"http://www.svensson.com/norge/sur1.htm\">surströmming</a>.\" (Warning: follow this link at your own risk! Ed.)</p>\n<p>I also read a newspaper article from Seattle (but I no longer have the link) about a restaurant that would <i>not</i> be serving lutefisk this year (which was certainly a disappointment to certain residents of the Ballard neighborood). It mentioned the Norsefest in Madison, MN, and wondered if the lutefisk eating contest and the outhouse race there had something to do with one another.</p>\n<p>Lutefisk was, however, still available at certain <a href=\"http://iowa.weblogger.com/2001/12/30\">fine shops in Iowa</a>.<br />\n<hr />\nEveryone else may be working with <a href=\"http://radio.userland.com\">Radio</a> today, but today was my day for laying cable. Twisted pair ethernet cable, that is... from our home office, through the wall to Christopher's room, down a PVC pipe in the wall two stories to the basement. I even crimped on the ends myself, and added a second hub. All I can say is... it worked, first try. I'm posting from the basement right now. And now I know the <a href=\"http://www.escape.ca/~droopy/ethernetcables.html\">pin outs</a> of the 8 wires in a twisted pair cable: white-orange, orange, white-green, blue, white-blue, green, white-brown, brown. Not bad for a guy who's color blind!</p>"},"next":{"fields":{"title":"The K Answer","date":"11 Jan 2002","slug":"/2002-01-11-the-k-answer/"}},"previous":{"fields":{"title":"Who is Edmund Stoiber?","date":"12 Jan 2002","slug":"/2002-01-12-who-is-edmund-stoiber/"}}},{"node":{"fields":{"title":"The K Answer","date":"11 Jan 2002","slug":"/2002-01-11-the-k-answer/","tags":"Y2002"},"html":"<p>BBC: <a href=\"http://news.bbc.co.uk/hi/english/world/europe/newsid_1755000/1755686.stm\">German right settles election rivalry</a><br />\nBBC: <a href=\"http://news.bbc.co.uk/hi/english/world/europe/newsid_1756000/1756007.stm\">Edmund Stoiber: Bavaria's 'pitbull'</a><br />\nSo it's Stoiber after all. A safer but less interesting choice than Merkel, but in my opinion the easier opponent for Schröder, especially since the economy seems to be bottoming out right now and the election is not until September.</p>"},"next":{"fields":{"title":"Notes Sucks","date":"11 Jan 2002","slug":"/2002-01-11-notes-sucks/"}},"previous":{"fields":{"title":"Leftover Lutefisk","date":"12 Jan 2002","slug":"/2002-01-12-leftover-lutefisk/"}}}],"pathPrefix":"","first":false,"last":false,"index":949,"pageCount":1101}}

/***/ })

